#towerSelection {
  background-color: #0000004d;
  flex-direction: row;
  font-family: Kanit, sans-serif;
  display: none;
  position: absolute;
}

.tower {
  border-style: solid;
  border-width: thin;
  border-radius: 6px;
  flex-direction: row;
  padding: 5px;
  font-size: 48px;
  display: flex;
}

.selected {
  background-color: #00000080;
  border-style: solid;
  border-radius: 6px;
  font-weight: bold;
}

.imageOuter {
  text-align: center;
  justify-content: center;
  display: flex;
}

.towerIcon {
  height: auto;
  width: 100%;
  margin: auto;
  display: block;
}

.towerInfo {
  width: 100px;
  flex-direction: column;
  display: flex;
}

.cost {
  text-align: center;
  flex-direction: row;
  justify-content: center;
  font-size: 20px;
  font-weight: bold;
  display: flex;
  position: relative;
}

.costText {
  text-shadow: -1px 1px #fff, 1px 1px #fff, 1px -1px #fff, -1px -1px #fff;
  margin-block: auto;
  position: absolute;
  top: 55%;
  left: 50%;
  transform: translate(-55%, -50%);
}

.tower .towerHoverText {
  visibility: hidden;
  width: max-content;
  color: #fff;
  text-align: center;
  z-index: 1;
  opacity: 0;
  background-color: #000;
  border-radius: 6px;
  padding: 5px;
  font-size: 16px;
  transition: opacity 1s ease-in-out;
  position: relative;
  top: -100%;
  left: 10%;
}

.tower:hover .towerHoverText {
  visibility: visible;
  opacity: 1;
}

